import React from "react";
import { Link } from "react-router-dom";
import styles from "./project.module.css";

const Project = ({ slides }) => {
  return (
    <div className={styles.wrapper}>
      <div className={styles.colorProd}> </div>
      <img src={slides.image} className={styles.imgProd} alt={slides.name} />
      <div className={styles.infoProd}>
        <p className={styles.nombreProd}>{slides.name}</p>
        <p className={styles.extraInfo}>{slides.description}</p>
      </div>
      <div className={styles.actions}>
        <div className={styles.center}>
          <Link to={slides.url}>
            <button className={styles.button17}>
              Explorar
            </button>
          </Link>
        </div>
      </div>
    </div>

    // <Link to={slides.url} className="profile">
    //   <article className={styles.article}>
    //     <picture className={styles.picture}>
    //       <source media="(min-width: 0px)" srcSet={slides.image} />
    //       <img src={slides.image} alt="background" />
    //     </picture>
    //   </article>

    //   <h1 className={styles.header}>{slides.name} </h1>
    // </Link>
  );
};

export default Project;
